<template>
  <div class="myRate">
    <!-- <div class="con">
      <ul>
        <li v-for="(item,index) in list" :key="index" @click="conFn(item)">{{item.value}}</li>
      </ul>
    </div>-->

    <svg class="setWH" v-for="(item,index) in list" :key="index" @click="conFn(index)">
      <use v-if="item" xlink:href="./star.svg#star3" />
      <use v-else xlink:href="./star.svg#star4" />
    </svg>

    <!-- <div class="setWH2" v-for="(item,index) in list" :key="index" @click="conFn(index)">
      <img v-if="item" src="./star2.svg" alt />
      <img v-else src="./star1.svg" alt />
    </div>-->
  </div>
</template>

<script>
import Vue from "vue";
export default {
  name: "myRate",
  components: {},
  data() {
    return {
      isShow: false,
      titText: "",
      list: []
    };
  },
  // props: {
  //   aaa: {
  //     type: Number,
  //     default: 100
  //   },
  //   list: {
  //     type: Array,
  //     default: [{ value: "blank" }]
  //   }
  // },
  props: {
    count: {
      type: Number,
      default: 5
    },
    value: {
      type: Number,
      default: 1
    }
  },
  created() {
    this.list.length = this.count;
    this.list.fill(false);
    this.list.forEach((item, itemIndex) => {
      console.log(item, itemIndex);
      if (itemIndex < this.value) {
        Vue.set(this.list, itemIndex, true);
      } else {
        Vue.set(this.list, itemIndex, false);
      }
    });
    // this.list[0] = false;
  },
  methods: {
    titFn() {
      this.isShow = !this.isShow;
    },
    conFn(index) {
      if (this.list[index]) {
        this.list.forEach((item, itemIndex) => {
          console.log(item, itemIndex);
          if (itemIndex < index) {
            Vue.set(this.list, itemIndex, true);
          } else {
            Vue.set(this.list, itemIndex, false);
          }
        });
      } else {
        this.list.forEach((item, itemIndex) => {
          console.log(item, itemIndex);
          if (itemIndex <= index) {
            Vue.set(this.list, itemIndex, true);
          } else {
            Vue.set(this.list, itemIndex, false);
          }
        });
      }

      // this.list[index] = false;
      // Vue.set(this.list, index, false);
      // // console.log(index);
      // console.log(this.list);

      // this.list.forEach((item, itemIndex) => {
      //   console.log(item, itemIndex);

      //   if (itemIndex < index) {
      //     Vue.set(this.list, itemIndex, !item);
      //   }
      // });
      // console.log(this.list);
    },
    toHome() {
      this.$emit("returnBack", "还你200块");
    }
  }
};
</script>

<style lang="less">
.myRate {
  .setWH {
    width: 60px;
    height: 60px;
  }
  .setWH2 {
    width: 60px;
    display: inline-block;
    img {
      width: 60px;
      height: 60px;
    }
  }
}
</style>

