<template>
  <div class="mySelect">
    <div class="tit" @click="titFn">{{titText}}</div>
    <div class="con" v-show="isShow">
      <ul>
        <li v-for="(item,index) in list" :key="index" @click="conFn(item)">{{item.value}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "mySelect",
  components: {},
  data() {
    return {
      isShow: false,
      titText: ""
    };
  },
  // props: {
  //   aaa: {
  //     type: Number,
  //     default: 100
  //   },
  //   list: {
  //     type: Array,
  //     default: [{ value: "blank" }]
  //   }
  // },
  props: ["list"],
  methods: {
    titFn() {
      this.isShow = !this.isShow;
    },
    conFn(item) {
      this.titText = item.value;
      this.isShow = false;
      this.$emit("getValue", item.value);
    }
  }
};
</script>

<style lang="less">
@import "../assets/reset.less";
.mySelect {
  .tit {
    width: 100%;
    height: 40px;
    line-height: 40px;
    border: 1px solid #000;
  }
  .con {
    ul {
      border-top: 1px solid #ccc;
      border-left: 1px solid #ccc;
      li {
        border-bottom: 1px solid #ccc;
        border-right: 1px solid #ccc;
        height: 40px;
        line-height: 40px;
      }
    }
  }
}
</style>

